










































import { reactive, defineComponent } from "@vue/composition-api";
import { userStore } from "@/store";

export default defineComponent({
  name: "Login",

  setup(props, { root }) {
    const form = reactive({
      username: "",
      password: ""
    });

    function register() {
      userStore.mutations
        .register(form)
        .then(() => root.$router.push({ name: "Login" }))
        .catch(err => {
          root.$toasted.error("Error registering: " + err.message);
        });
    }

    return {
      form,
      register
    };
  }
});
